@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;500;700&family=Source+Code+Pro:wght@300;500;700&display=swap);
:root {
  --color-primary: #e53e29;
  --color-secondary: #262626;
  --color-secondary-light: rgb(85, 85, 85,0.3);
  --color-secondary-dark: #131313;
  --color-secondary-inverted: rgb(85, 85, 85);
  --gradient-primary-180deg: linear-gradient(180deg, #baeaff, #61C9F7);
  --color-quaternary: #262626;
  --color-quinary: #fff;
  
}


body {
  overflow-x: hidden;
  background: rgb(38, 38, 38);
  background-image: url(/briefcase/static/media/back.c2d7ee12.png);
  background-size: 210% 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  color: #fff;
  color: var(--color-quinary);
  margin: 0;
  font-family: 'Roboto Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#tsparticles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.navbar{
    top: 0;
    left: 0;
    border-radius: 0;
    position: fixed;
    width: 97%;
    background: var(--color-secondary-light);
    border-bottom: 0px solid var(--color-quaternary);
    border-right: 0px solid var(--color-quaternary);
    padding: 15px 0 15px 0;
    z-index: 2;
    transition: all 1s ease-in-out;
}

.nav-menu-open{
    width: 78%;
}

.nav-menu-close{
    width: 100%;
}

.navbar ul{
    list-style: none;
    margin: 0;
    padding: 0 8rem 0 8rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.navbar ul li{
    margin-right: 20px;
    cursor: pointer;
}

.navbar ul li a,
.navbar ul li span,
.toggle-menu{
    color: var(--color-quinary);
    text-decoration: none;
    margin-right: 4px;
    padding: 0 4px 0 4px;
    border: transparent solid 4px;
    transition: all 0.3s;
}

.navbar ul li a,
.navbar ul li span{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    grid-gap: 8px;
    gap: 8px;
}

.navbar ul li p{
    margin: 0;
    padding: 0;
}

.navbar ul li a:hover,
.navbar ul li span:hover{
    border: var(--color-primary) solid 4px;
    border-radius: 1.2rem;
    color: var(--color-quinary);
}

.toggle-menu{
    cursor: pointer;
    position: absolute;
    right: 1rem;
}


@media (max-width: 640px) {

    .nav-menu-open{
        width: 99%;
    }

    .navbar{
        width: 100%;
        position: fixed;
        z-index: 40;
    }

    .navbar ul{
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
        margin-left: 15px;
        padding: 0;
        
    }
    .navbar ul li{
        margin-right: 0;
    }

    .navbar ul li a{
        flex-direction: column;
    }
}
    

.side-right-menu{
    background: var(--color-secondary-light);
    width: 22%;
    height: 100vh;
    position: fixed;
    top: 0;
    right: 0;
    font-family: 'Source Code Pro', monospace;
    z-index: 35;
    transition: all 1.5s ease-in-out;
}

.logo{
    border-radius: 0;
    width: 100%;
    opacity: 0.9;
    height: 280px;
}


.menu-close{
    opacity: 0;
    width: 0;
    z-index: -5;
}

.side-right-menu p:nth-child(3){
    border-radius: 15px;
    background-color: var(--color-quaternary);
    color: var(--color-quinary);
    text-align: center;
    margin: 1rem;
    padding: 1rem;
    overflow: hidden;
    height: 222px;
}

.consoleRoot{
    
    margin: 0;
}

.side-right-menu ul{
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    position: absolute;
    justify-content: center;
    bottom: 15px;
    right: 0;
    left: 0;
}

.side-right-menu ul li{
    margin: 0.5rem;
}

.side-right-menu ul li i{
    cursor: pointer;
    font-size: 2rem;
}

.side-right-menu ul li a{
    text-decoration: none;
    color: var(--color-quinary);
    font-size: 1.5rem;
}

h1{
    font-family: 'Source Code Pro', monospace;
}


@media (max-width: 640px) {

    .menu-close{
        opacity: 0;
        width: 0;
        z-index: -5;
        display: none;
    }

    .back-side{
        background-color: rgb(0, 0, 0,0.7);
        width: 100%;
        height: 100vh;
        position: fixed;
    }

    .side-right-menu{
        background: var(--color-secondary);
        position: absolute;
        top: 94px;
        width: 90%;
        height: 110vh;
        z-index: 35;
        border-bottom: 4px solid black;
    }
}

.main-menu-open{
    width: 78%;
}

.main-menu-close{
    width: 100%;
}

.container-main{
    padding-top: 62px;
    transition: all 1s ease-in-out;
}



@media (max-width: 640px) {


    .container-main{
        padding-top: 94px;
        transition: all 1s ease-in-out;
    }

    .main-menu-open{
        width: 100%;
    }

    .back-overflow{
        height: 100vh;
        overflow: auto;
    }
}
    
.App {
  text-align: center;
}



.faq {
    display: flex;
    flex-direction: column;
    background: rgb(38, 38, 38,0.8);
}

.faq h1 {
    margin: 0;
}


@-webkit-keyframes leftToRight {
    from {
        left: -100%;
        opacity: 25%;
    }
    to {
        opacity: 100%;
        left: 0%;
    }
}


@keyframes leftToRight {
    from {
        left: -100%;
        opacity: 25%;
    }
    to {
        opacity: 100%;
        left: 0%;
    }
}

@-webkit-keyframes rightToLeft {
    from {
        right: -100%;
        opacity: 25%;
    }
    to {
        opacity: 100%;
        right: 0%;
    }
}

@keyframes rightToLeft {
    from {
        right: -100%;
        opacity: 25%;
    }
    to {
        opacity: 100%;
        right: 0%;
    }
}

.where {
    position: relative;
    padding-right: 35%;
    margin: 1rem 2rem 0.5rem 2rem;
    -webkit-animation: leftToRight 3s ;
            animation: leftToRight 3s ;
}


.what {
    position: relative;
    padding-left: 35%;
    margin: 1rem 2rem 0.5rem 2rem;
    -webkit-animation: rightToLeft 3s ;
            animation: rightToLeft 3s ;
}

.what-hope {
    position: relative;
    padding-right: 35%;
    margin: 1rem 2rem 0.5rem 2rem;
    -webkit-animation: leftToRight 3s ;
            animation: leftToRight 3s ;
}


.where .divisor,
.what-hope .divisor {
    position: absolute;
    top: 2.2rem;
    border-bottom: var(--color-primary) solid 4px;
    width: 90%;
}



.what .divisor {
    position: absolute;
    top: 2.2rem;
    right: 0;
    border-bottom: var(--color-primary) solid 4px;
    width: 90%;
}




.where p,
.what p,
.what-hope p {
    margin-top: 1.3rem;
    text-align: justify;
    font-size: 1.5em;
}

.where p > i,
.what p > i{
    color: var(--color-primary);
    font-style: normal;
    font-weight: bold;
}

@media (max-width: 640px) {
    .where,
    .what,
    .what-hope {
        padding-left: 0;
        padding-right: 0;
    }
}
.skills{
    background: var(--color-secondary-inverted);
