.presentation-card{
    background-color: var(--color-secondary-dark);
    position: relative;
    margin-left: 5px;
    border-radius: 25px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    margin-top: 30px;
    margin-bottom: 20px;
    transition: all 1.1s ease-in-out;
}

.presentation-menu-open{
    width: 44%;
}

.presentation-menu-close{
    width: 30%;
}

.desktop-and-drescription{
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
}

.presentation-card  .desktop-and-drescription .description{
    display: flex;
    flex-direction: column;
}


.presentation-card .desktop-and-drescription .description h4{
    font-size: 1.1rem;
    font-weight: bold;
    padding: 0;
    margin: 0 0 0.5rem 0;
}

.presentation-card .desktop-and-drescription .description p{
    font-size: 0.9rem;
    height: 80px;
    padding: 0 0.5rem 0 .5rem;
    text-align: justify;
    overflow: auto;
}

.top-button-description{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}


.urls{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 0 1.2rem 1rem 1.2rem;
}

.top-button-description .urls .switchButton{
    border-style: none;
    font-size: 1.1rem;
    width: 50%;
    font-weight: bold;
    padding: 0.65rem 1rem 0.54rem 1rem;
    border-bottom-left-radius: 1.5rem;
    border-bottom: 2px solid var(--color-primary);
    border-left: 2px solid var(--color-primary);
    border-top: none;
    background-color: var(--color-primary);
    cursor: pointer;
    color: var(--color-quinary);
    transition: all 0.15s ease-in-out;
}

.top-button-description .urls .switchButton div{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.fa-hand-pointer{
    -webkit-transform: rotate(45deg) scale(1.5) translateY(-0.4rem);
            transform: rotate(45deg) scale(1.5) translateY(-0.4rem);
}

.top-button-description .urls .project-url,
.top-button-description .urls .project-git{
    border-style: none;
    text-decoration: none;
    font-size: 1.3rem;
    width: 30%;
    padding: 0.2rem 1rem 0.55rem 1rem;
    border-bottom: 2px solid var(--color-primary);
    border-top: none;
    background-color: var(--color-primary);
    cursor: pointer;
    color: var(--color-quinary);
    transition: all 0.15s ease-in-out;
}

.top-button-description .urls .project-git{
    border-left: 2px solid var(--color-primary);
}
.top-button-description .urls .project-url{
    border-right: 2px solid var(--color-primary);
    border-bottom-right-radius: 1.5rem;
}


.top-button-description .switchButton:hover,
.top-button-description .urls .project-url:hover,
.top-button-description .urls .project-git:hover{
    background-color: var(--color-secondary-light);
}




.tecnologies{
    margin-top: 1rem;
    padding: 1rem 0 1rem 0;
